.header__nav-container {
    height: 119px;
    display: grid;
    grid-template-columns: minmax(min-content, max-content) minmax(min-content, 56px);
    grid-template-rows: min-content;
    column-gap: 24px;
    align-content: center;
}

@media (max-width: 934px) {
    .header__nav-container {
        padding-right: 27px;
    }
}